"use client";
import { useEffect, useState } from "react";
import Image from "next/image";
import { Search, User, Menu, X, Heart } from "lucide-react";

export default function Nav() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  // Detectar scroll para cambiar el estilo de la barra
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 20);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // Items del menú para no repetir código
  const navLinks = [
    { name: "Inicio", href: "#hero" }, // Asumiendo que el Hero está arriba
    { name: "Nosotros", href: "#about" },
    { name: "En Vivo", href: "#live", highlight: true }, // Resaltado
    { name: "Eventos", href: "#eventos" },
    { name: "Servicios", href: "#servicios" },
  ];

  return (
    <>
      <header
        className={`fixed inset-x-0 top-0 z-50 transition-all duration-500 border-b ${
          isScrolled
            ? "bg-[#050505]/80 backdrop-blur-xl border-white/10 py-3"
            : "bg-transparent border-transparent py-5"
        }`}
      >
        <div className="container flex items-center justify-between">
          
          {/* --- LOGO --- */}
          <a href="#" className="flex items-center gap-3 group">
            {/* Si no tienes logo.png aún, este div simula uno elegante */}
            <div className="w-10 h-10 rounded-xl bg-gradient-to-tr from-primary to-primary/50 flex items-center justify-center text-black font-bold text-xl shadow-[0_0_15px_rgba(19,194,194,0.4)] group-hover:scale-105 transition-transform">
              C
            </div>
            
            {/* Si tienes la imagen, descomenta esto y borra el div de arriba:
            <Image
              src="/logo.png"
              alt="Ciudad Avivamiento"
              width={40}
              height={40}
              className="rounded-full"
            />
            */}
            
            <div className="hidden sm:block leading-tight">
              <strong className="block text-white text-lg tracking-wide">CIUDAD</strong>
              <span className="block text-xs text-primary font-medium tracking-[0.2em] -mt-1">AVIVAMIENTO</span>
            </div>
          </a>

          {/* --- NAV DESKTOP --- */}
          <nav className="hidden lg:flex items-center gap-1 bg-white/5 p-1 rounded-full border border-white/5 backdrop-blur-sm">
            {navLinks.map((link) => (
              <a
                key={link.name}
                href={link.href}
                className={`px-5 py-2 rounded-full text-sm font-semibold transition-all duration-300 ${
                  link.highlight
                    ? "bg-red-600 text-white shadow-lg shadow-red-900/40 hover:bg-red-700"
                    : "text-white/70 hover:text-white hover:bg-white/10"
                }`}
              >
                {link.name}
              </a>
            ))}
          </nav>

          {/* --- ACCIONES DERECHA --- */}
          <div className="flex items-center gap-3">
            {/* Botón Donar (Destacado) */}
            <a 
              href="#donar" 
              className="hidden md:flex items-center gap-2 px-4 py-2 rounded-lg bg-white/5 border border-white/10 text-sm font-semibold hover:bg-primary hover:text-black hover:border-primary transition-all group"
            >
              <Heart size={16} className="text-primary group-hover:text-black transition-colors" />
              <span>Donar</span>
            </a>

            <button className="p-2 rounded-full hover:bg-white/10 text-white/70 hover:text-white transition-colors">
              <Search size={20} />
            </button>
            
            {/* Botón Menú Móvil */}
            <button 
              className="lg:hidden p-2 text-white"
              onClick={() => setMobileMenuOpen(true)}
            >
              <Menu size={28} />
            </button>
          </div>
        </div>
      </header>

      {/* --- MENÚ MÓVIL (Overlay) --- */}
      <div 
        className={`fixed inset-0 z-[60] bg-black/95 backdrop-blur-2xl transition-all duration-500 flex flex-col items-center justify-center gap-8 ${
          mobileMenuOpen ? "opacity-100 visible" : "opacity-0 invisible pointer-events-none"
        }`}
      >
        <button 
          className="absolute top-6 right-6 p-2 text-white/50 hover:text-white"
          onClick={() => setMobileMenuOpen(false)}
        >
          <X size={32} />
        </button>

        <nav className="flex flex-col items-center gap-6">
          {navLinks.map((link) => (
            <a
              key={link.name}
              href={link.href}
              onClick={() => setMobileMenuOpen(false)}
              className={`text-3xl font-bold tracking-tight ${
                link.highlight ? "text-red-500" : "text-white hover:text-primary"
              } transition-colors`}
            >
              {link.name}
            </a>
          ))}
          <a 
            href="#donar" 
            onClick={() => setMobileMenuOpen(false)}
            className="mt-4 px-8 py-3 rounded-full bg-primary text-black font-bold text-lg hover:shadow-[0_0_20px_rgba(19,194,194,0.4)] transition-all"
          >
            Donar Ahora
          </a>
        </nav>
      </div>
    </>
  );
}



